import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import Link from 'gatsby-link';
import media from '../../media';

import compose from 'recompose/compose';
import withStateHandlers from 'recompose/withStateHandlers';

import NavHamburger from './NavHamburger';

const NavWrapper = styled.div`
  background-color: #840124;
  color: #eee;
`;

const NavUl = styled.ul`
  display: flex;
  justify-content: center;
  margin: 0;

  ${media.mobile`
    display: ${props => props.isShowBar ? 'block' : 'none'};
    text-align: center;
  `}
`;

const NavLi = styled(Link)`
  list-style-type: none;
  line-height: 60px;
  text-decoration: none;
  font-weight: bold;
  padding: 0 8px;
  font-size: 1.2rem;
  color: #eee;

  ${media.desktop`
    padding: 0 12px;
  `};

  ${media.tablet`
    padding: 0 4px;
  `};

  ${media.mobile`
    display: block;
  `};
`;

const NavMobileBar = styled.div`
  display: none;
  justify-content: center;
  align-items: center;
  line-height: 60px;
  cursor: pointer;

  > .mobile-bar-label {
    padding-right: 8px;
    font-size: 22px;
  }

  ${media.mobile`
    display: flex;
  `}
`;

const NavBar = ({ isShowBar, handleShowBar }) => {
  return (
    <NavWrapper onClick={handleShowBar}>
      <NavMobileBar isShowBar={isShowBar}>
        <span className="mobile-bar-label">選單</span>
        <NavHamburger isShowBar={isShowBar} />
      </NavMobileBar>
      <NavUl isShowBar={isShowBar}>
        <NavLi to="/">首頁</NavLi>
        <NavLi to="/news/">最新活動</NavLi>
        <NavLi to="/about/">經營理念</NavLi>
        <NavLi to="/menu/">餐點介紹</NavLi>
        <NavLi to="/affiliate/">加盟</NavLi>
        <NavLi to="/contact/">聯絡我們</NavLi>
      </NavUl>
    </NavWrapper>
  );
};

NavBar.propTypes = {
  isShowBar: PropTypes.bool.isRequired,
  handleShowBar: PropTypes.func.isRequired
};

export default compose(
  withStateHandlers({ isShowBar: false }, {
    handleShowBar: ({ isShowBar }) => () => ({ isShowBar: !isShowBar })
  }),
)(NavBar);



